class RocketComponent extends React.Component {
	

	render() {
		const rockets = this.props.rockets;
		const rocketsArr = [];
		for (let key in rockets){
			rocketsArr.push(rockets[key]);
		};
		console.log(rocketsArr);

		return (
			<section className="section">
				<div className="container">
					<div className="columns is-multiline">
						{rocketsArr.map(rocket => (
							<RocketHeader
								rocket = {rocket}
							/>
						))}
					</div>
				</div>
			</section>
		);
	};
};

function RocketHeader(props) {
	return (
		<div className="column c-hover is-half">
			<div className="columns">
				<div className="column is-one-fifth" key={props.rocket.id}>
					<figure className="image is-128x128">
						<img class="is-rounded" src="https://bulma.io/images/placeholders/128x128.png"></img>
					</figure>
				</div>
				<div className="column is-primaru">
					<div className="notification">
						{props.rocket.name}
					</div>
				</div>
			</div>
		</div>
	);
};

function RocketDetails(props) {
	return (
		<div/>
	);
};

export default RocketComponent;